import vars from '../../../src/scripts/globals/variables'

export default class SearchFilter {
  constructor(el) {
    this.el = el

    this.setVariables()
    this.setUp()
    this.addListeners()
  }

  setVariables() {
    this.filters = document.getElementById(
      this.el.getAttribute('aria-controls')
    )
  }

  setUp() {
    this.filters.hidden = vars.mq.matches ? false : true
    if (!vars.mq.matches) {
      this.el.setAttribute('aria-expanded', false)
    }
  }

  addListeners() {
    vars.mq.addListener(this.setUp.bind(this))
    this.el.addEventListener('click', this.toggleFilters.bind(this))
  }

  openMenu() {
    if (!vars.mq.matches) {
      this.el.setAttribute('aria-expanded', 'true')
      this.filters.hidden = false
    }
  }

  closeMenu() {
    if (!vars.mq.matches) {
      this.el.setAttribute('aria-expanded', 'false')
      this.filters.hidden = true
    }
  }

  toggleFilters(event) {
    event.preventDefault()
    this.filters.hidden ? this.openMenu() : this.closeMenu()
  }
}



